<template>
  <div class="container">
    <!-- top begin  -->
    <Navigation></Navigation>
    <!-- top end    -->

    <!-- left begin  -->
    <Aside></Aside>
    <!-- left end   -->

    <!-- main begin  -->
    <div class="main">
      <router-view/>
    </div>
    <!-- main end    -->
    <Password></Password>
  </div>
</template>

<script>
import Password from './pwd'
import Aside from './layouts/Aside'
import Navigation from './layouts/Navigation'

export default {
  // 组件
  components: {
    Aside,
    Navigation,
    Password
  },
  data() {
    return {}
  }
}
</script>


<style scoped>
.container {
  width: 100%;
  height: 100%;
}
.container .main {
  position: absolute;
  left: 60px;
  top: 60px;
  right: 0;
  bottom: 0;
  overflow-x: auto;
  overflow-y: auto;
}
</style>



