import Vue from 'vue'
import Vuex from 'vuex'
import axios from '../util/ajax'

Vue.use(Vuex)

/* 全局的状态（数据源） */
const state = {
  pwdDialog: false
}
/* 状态的提交--变更状态 */
const mutations = {
  SET_PASSWORD_STATE(state, pwdSta) {
    state.pwdDialog = pwdSta
  }
}
/* 过滤并计数 */
const getters = {
  get_password_state_action(state) {
    return state.pwdDialog
  }
}
/* Action提交的是 mutation */
const actions = {
  // 修改密码层 是否隐藏
  set_password_state_action(state, params) {
    state.commit('SET_PASSWORD_STATE', params)
  },
}

export default new Vuex.Store({
  state,
  mutations,
  getters,
  actions
})



