<template>
  <div class="nav-wrap">
    <el-header style="text-align: right; font-size: 12px">
      <div class="logo"><img src="../../assets/mobgi_logo.png" alt="logo" /></div>
      <div class="system-wrap">
        <el-dropdown>
          <div class="dropdown-name">{{system}}
            <i class="el-icon-caret-bottom"></i>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="(item, key) in modules.module" :command="key" :key="key">
              <a class="dropdown-a" :href="item.url">{{item.name }}</a>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>

      <el-dropdown @command="handleCommand">
        <div class="dropdown-name">{{modules.user_name}}
          <i class="el-icon-caret-bottom"></i>
        </div>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="pwd">修改密码</el-dropdown-item>
          <el-dropdown-item command="logout">
            <a class="dropdown-a" href="/Admin/Login/index">注销</a>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>

  </div>
</template>

<script>
export default {
  // 组件
  components: {},
  created: function() {
    this.getModule()
  },
  data() {
    return {
      system: 'ChannelReport',
      accounts: [],
      modules: {}
    }
  },
  methods: {
    // 显示 修改密码层
    handleCommand(command) {
      if (command == 'pwd') {
        this.$store.dispatch('set_password_state_action', true)
      }
    },

    // 获取系统列表
    getModule() {
      var that = this
      this.$axios
        .get('/Admin/Home/getModule', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.modules = respData.data
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          console.log('获取系统列表:', err)
        })
    }
  }
}
</script>


<style scoped>
.nav-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  height: 60px;
  background: #303133;
  color: #fff;
  font-size: 16px;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}
.dropdown-name {
  color: #fff;
  font-size: 16px;
  margin-right: 36px;
  height: 60px;
  line-height: 60px;
}
.system-wrap {
  float: left;
  height: 60px;
  line-height: 60px;
  margin-left: 20px;
}

.account-wrap {
  float: left;
  height: 60px;
  line-height: 60px;
  margin-left: 20px;
}
.account-wrap .account-text {
  float: left;
  line-height: 60px;
  padding: 0 10px;
  font-size: 16px;
}

.logo {
  height: 60px;
  float: left;
  margin-left: -14px;
  display: inline-block;
}
.logo img {
  width: 50px;
  height: 50px;
  margin-top: 9px;
}

.dropdown-a {
  list-style: none;
  line-height: 36px;
  padding: 0;
  margin: 0;
  font-size: 14px;
  color: #606266;
  cursor: pointer;
  outline: 0;
  text-decoration: none;
}
</style>



