<template>
  <TabNavigation :tabItems="tabItems"></TabNavigation>
</template>

<script>
import TabNavigation from '../../components/TabNavigation'
export default {
  // 组件
  components: {
    TabNavigation
  },
  data() {
    return {
      tabItems: {
        activeName: 'channelReport',
        tabHost: '/channelreport/#/home/',
        items: [
          { label: '渠道报表', name: 'channelReport' },
          { label: '分包报表', name: 'packageReport' }
        ]
      }
    }
  },
  created: function() {
    var that = this
    var list = this.tabItems.items
    var path = (this.$route.path + '/').toLowerCase()
    for (var k in list) {
      var name = list[k]['name']
      if (path.indexOf('/home/' + name.toLowerCase() + '/') == 0) {
        that.tabItems.activeName = name
        break
      }
    }
  },
  watch: {
    $route(to, from) {
      var that = this
      var list = this.tabItems.items
      var path = (to.path + '/').toLowerCase()
      for (var k in list) {
        var name = list[k]['name']
        if (path.indexOf('/home/' + name.toLowerCase() + '/') == 0) {
          that.tabItems.activeName = name
          break
        }
      }
    }
  }
}
</script>


<style scoped>
.tab-container-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.tab-title-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  box-sizing: border-box;
}
.tab-content-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 42px;
  right: 0;
  bottom: 0;
  width: 100%;
}
</style>



