<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import userPath from './router/fullpath'
import * as util from './util/util.js'

//请求拦截句柄
let myInterceptor

export default {
  components: {},
  name: 'App',
  data() {
    return {
      menuData: null,
      userData: null
    }
  },
  methods: {
    getdialogVisible() {},
    getCookie(name) {
      var arr,
        reg = new RegExp('(^| )' + name + '=([^;]*)(;|$)')
      if ((arr = document.cookie.match(reg))) return unescape(arr[2])
      else return null
    },
    getRoutes: function(menus) {
      let vm = this
      let allowedRouter = []
      //将菜单数据转成多维数组格式
      let arrayMenus = util.buildMenu(menus)
      //console.log('arrayMenus:', arrayMenus)
      //将多维数组转成对象格式
      let hashMenus = {}
      let setMenu2Hash = function(array, base) {
        array.map(key => {
          if (key.route) {
            let hashKey = ((base ? base + '/' : '') + key.route).replace(
              /^\//,
              ''
            )
            hashMenus['/' + hashKey] = true
            if (Array.isArray(key.children)) {
              console.log('key.children:', key.children)
              console.log('key.route:', key.route)
              setMenu2Hash(key.children, key.route)
            }
          }
        })
      }
      setMenu2Hash(arrayMenus)
      console.log(hashMenus)
      //全局挂载hashMenus，用于实现路由守卫
      this.$root.hashMenus = hashMenus
      //筛选本地路由方法
      let findLocalRoute = function(array, base) {
        //  console.log('筛选本地路由方法:', array)
        let replyResult = []
        array.forEach(function(route) {
          let pathKey = (base ? base + '/' : '') + route.path
          console.log('base:', base)
          console.log('pathKey:', pathKey)
          if (hashMenus[pathKey]) {
            if (Array.isArray(route.children)) {
              route.children = findLocalRoute(route.children, route.path)
            }
            console.log('route:', route)
            replyResult.push(route)
          }
        })
        if (base) {
          return replyResult
        } else {
          allowedRouter = allowedRouter.concat(replyResult)
        }
      }
      let originPath = util.deepcopy(userPath)
      findLocalRoute(originPath)
      return allowedRouter
    },
    signin(callback) {
      let that = this
      let $axios = this.$axios
      let routersList
      $axios
        .get('/Admin/Index/getRouter', {
          params: { module: 'Channelreport_Monitor_Module' }
        })
        .then(function(response) {
          var respData = response.data
          if (respData.success) {
            var allowedRouter = that.getRoutes(respData.data)
            console.log('allowedRouter:', allowedRouter)
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
      typeof callback === 'function' && callback()
    },
    setInterceptor: function(resourcePermission) {
      let vm = this
      myInterceptor = this.$axios.interceptors.request.use(function(config) {
        //得到请求路径
        let perName = config.url
          .replace(config.baseURL, '')
          .replace('/GET', '')
          .replace('/POST', '')
          .split('?')[0]

        console.log('得到请求路径:', perName)

        //权限格式1 /path/${param}
        let reg1 = perName.match(/^(\/[^\/]+\/)[^\/]+$/)
        if (reg1) {
          perName = reg1[1] + '**'
        }
        //权限格式2 /path/${param}/path
        let reg2 = perName.match(/^\/[^\/]+\/([^\/]+)\/[^\/]+$/)
        if (reg2) {
          perName = perName.replace(reg2[1], '*')
        }
        //校验权限
        if (!resourcePermission[config.method + ',' + perName]) {
          //调试信息
          console.warn(resourcePermission, config.method + ',' + perName)
          vm.$message({
            message: '无访问权限，请联系企业管理员',
            type: 'warning'
          })
          //拦截请求
          return Promise.reject({
            message: 'no permission'
          })
        }
        return config
      })
    }
  },
  created: function() {
    //this.signin()
  }
}
</script>

<style>
.app-wrap {
  height: 100%;
  width: 100%;
}

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
}
* {
  margin: 0;
  padding: 0;
}
/*设置IOS页面长按不可复制粘贴，但是IOS上出现input、textarea不能输入，因此将使用-webkit-user-select:auto;*/
* {
  /* -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; */
}
input,
textarea {
  -webkit-user-select: auto; /*webkit浏览器*/
  margin: 0px;
  padding: 0px;
  outline: none;
}
ul,
li {
  list-style: none;
}
img {
  vertical-align: top;
}
a,
input,
button {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-appearance: none;
  border-radius: none;
}
html,
body {
  position: relative;
  width: 100%;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'PingFang SC',
    'Helvetica Neue', STHeiti, 'Microsoft Yahei', Tahoma, Simsun, sans-serif;
}
</style>



