<template>
  <div class="tab-container-wrap">
    <div class="tab-title-wrap">
      <el-tabs v-model="tabItems.activeName"  @tab-click="handleClick">
        <el-tab-pane
          v-for="(item, index) in tabItems.items"
          :key="index"
          :label="item.label"
          :name="item.name"
        >
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="tab-content-wrap">
        <router-view></router-view>
    </div>
  </div>

</template>

<script>
/* import Aside from './layouts/Aside'
import Navigation from './layouts/Navigation' */
export default {
  // 组件
  components: {},
  data() {
    return {}
  },
  props: ['tabItems'],
  methods: {
    handleClick(tab, event) {
      //console.log(tab, event)
      //console.log(this.tabItems.tabHost)
      if (tab.active) {
        window.location.href = this.tabItems.tabHost + tab.name
      }
    }
  },
  computed: {
    tabActiveName: function() {}
  }
}
</script>


<style scoped>
.tab-container-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.tab-title-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  box-sizing: border-box;
}
.tab-content-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 42px;
  right: 0;
  bottom: 0;
  width: 100%;
}
</style>



