import TabSetting from '../page/home/index'

const home = [{
    path: '/',
    component: TabSetting,
    redirect: '/channelreport',
    children: [{
      path: '/',
      component: r =>
        require.ensure([], () => r(require('../page/home/channelReport')))
    }]
  },
  {
    path: '/home',
    component: TabSetting,
    redirect: '/channelreport',
    children: [{
        path: '/',
        component: r =>
          require.ensure([], () => r(require('../page/home/channelReport')))
      },
      {
        path: 'channelReport',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/home/channelReport')),
            'channelReport'
          )
      },
      {
        path: 'packageReport',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/home/packageReport')),
            'packageReport'
          )
      }
    ]
  }
]

export default home



