<template>
  <el-dialog title="修改密码" :visible.sync="dialogFormVisible">
    <el-form :model="form">
      <el-form-item label="当前密码" :label-width="formLabelWidth">
        <el-input type="password" :minlength="minlength" :maxlength="maxlength" v-model="form.current_password" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="新密码" :label-width="formLabelWidth">
        <el-input type="password" :minlength="minlength" :maxlength="maxlength" v-model="form.password" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="重复密码" :label-width="formLabelWidth">
        <el-input type="password" :minlength="minlength" :maxlength="maxlength" v-model="form.r_password" auto-complete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="hiddenDialog">取 消</el-button>
      <el-button type="primary" @click="submitForm">确 定</el-button>
    </div>
  </el-dialog>

</template>

<script>
export default {
  created: function() {
    // this.dialogFormVisible = this.password_state
    //     token:b3bd94f0
    // current_password:admin123
    // password:admin
    // r_password:admin
  },
  watch: {
    password_state(val) {
      this.dialogFormVisible = val
    }
  },
  computed: {
    password_state() {
      return this.$store.getters.get_password_state_action
    }
  },
  data() {
    return {
      dialogFormVisible: false,
      formLabelWidth: '120px',
      minlength: 6,
      maxlength: 16,
      form: {
        current_password: '',
        password: '',
        r_password: ''
      }
    }
  },
  methods: {
    hiddenDialog() {
      this.$store.dispatch('set_password_state_action', false)
    },
    submitForm() {
      var that = this

      if (that.form.current_password.length == 0) {
        that.$message.error('请输入当前密码')
        return false
      }

      if (that.form.password.length > 16 || that.form.password.length < 6) {
        that.$message.error('请输入6-16位的密码')
        return false
      }

      if (that.form.password != that.form.r_password) {
        that.$message.error('输入密码不一致')
        return false
      }

      var params = this.form
      that.$axios
        .post('/Admin/User/passwdPost', params)
        .then(function(response) {
          var respData = response.data
          that.$message(respData.msg)
          if (respData.code == 0) {
            that.hiddenDialog()
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    }
  }
}
</script>


<style scoped>

</style>



